<template>
  <div class="score">
    {{ date }}
    {{ team }}
    <template v-if="scores !== 'committee'">
      <input type="number"  v-for="score in scores" v-bind:key="score.date + score.team" v-bind:value="score" v-on:change="changeScore">
    </template>
    <span v-if="scores === 'committee'">Committee</span>
  </div>
</template>

<script>
  import Socket from '../socket.js';

  export default {
    name: 'Score',
    props: ['data'],
    data: function () {
      return this.data;
    },
    methods: {
      changeScore: function() {
        let inputs = this.$el.querySelectorAll('input[type=number]');
        this.data.scores = [...inputs].map(i => parseInt(i.value, 10));
        Socket.send('score:' + JSON.stringify(this.data));
      }
    }
  }
</script>

<style>
  .score input {
    width: 3em;
  }
</style>
